/* App.css */
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow: hidden; /* Prevents scrolling on the main page */
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

#root {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.main-container {
  flex: 1;
  overflow-y: auto; /* Enables scrolling within the main container if content overflows */
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f0f0f0;
}
